.footer {
	// height: 200px
	color: $grey-500;
	text-align: center;
	&-i {
		margin-top: 20px;
		padding-top: 37px;
		padding-bottom: 45px;
		position: relative;
		@include min-screen($tablet) {
			padding-top: 31px; }
		@include min-screen($desctop) {
			padding-top: 40px; }
		&:before {
			content: '';
			left: 24px;
			right: 24px;
			position: absolute;
			top: 0;
			height: 2px;
			background-image: linear-gradient(to left, $green-400, $blue-400);
			@include min-screen($tablet) {
				left: $base-padding * 3;
				right: $base-padding * 3; }
			@include min-screen($desctop) {
				left: $base-padding * 2;
				right: $base-padding * 2; } } }
	b {
		font-weight: normal;
		color: $black-300; }
	&-contacts {
		font-style: normal;
		@include min-screen($desctop) {
			text-align: left; }
		&-top {
			font-size: $fs-xm;
			line-height: 30px;
			margin-bottom: 20px;
			@include min-screen($tablet) {
				margin-bottom: 20px; }
			@include min-screen($desctop) {
				margin-bottom: 7px; }
			&-item {
				display: block;
				padding-right: 6px;
				@include min-screen(500) {
					display: inline-block; }
				& + & {
					padding-left: 10px;
					@include min-screen(500) {
						border-left: 1px solid $grey-500;
						&:last-child {
							display: block;
							border-left: none;
							margin-top: 8px; } }
					@include min-screen($tablet) {
						&:last-child {
							display: inline-block;
							border-left: 1px solid $grey-500;
							margin-bottom: 0; } } } } }
		&-bot {
			font-size: $fs-s;
			line-height: $lh-sm;
			margin-bottom: 24px;
			@include min-screen($tablet) {
				margin-bottom: 29px; }
			@include min-screen($desctop) {
				margin-bottom: 50px; } } }
	&-share {
		// padding-top: 5px
		text-align: center;
		margin-bottom: 21px;
		@include min-screen($desctop) {
			margin-bottom: 0;
			text-align: right;
			height: 0; }
		img {
			display: inline-block; } }
	&-copy {
		font-size: $fs-xs;
		text-align: center;
		line-height: $lh-sm;
		@include screen($tablet, $desctop) {
			line-height: 1; }
		a {
			text-decoration: none; }
		&-dev {
			@include screen($tablet, $desctop) {
				display: inline-block;
				flex-basis: auto; }
			@include min-screen($desctop) {
				text-align: left; } }
		&-owner {
			@include screen($tablet, $desctop) {
				display: inline-block;
				flex-basis: auto;
				border-left: 1px solid $grey-500; }
			@include min-screen($desctop) {
				text-align: right; } } } }
